import React, { useContext, useEffect, useState } from 'react';
import { useFormContext } from 'react-hook-form';
import { SubjectContext } from '../../context/SubjectContext';
import IntentionForm from '../Forms/IntentionForm';

const EducationalIntention = () => {
  const { subject } = useContext(SubjectContext);
  const intetion = subject?.educationalIntention || '';
  const {
    formState: { isValid, isSubmitting },
    setValue,
  } = useFormContext();

  useEffect(() => {
    if (subject?.educationalIntention) {
      setValue(
        'educationalIntention',
        subject?.educationalIntention,
        {
          shouldValidate: true,
        }
      );
    }
  }, [subject]);

  return (
    <>
      <p>
        En esta seccin se describe brevemente cmo contribuye tu
        Unidad de aprendizaje al perfil del egresado
      </p>
      <p className="font-bold">Ejemplo</p>
      <p>
        La unidad de aprendizaje Gentica contribuye al perfil de
        egreso de la Licenciatura en Biologa desarrollando
        conocimiento de los mecanismos hereditarios y de control
        gentico de los organismos, sentando las bases para el manejo,
        mejoramiento e investigacin de los seres vivos. Asimismo,
        fomenta habilidades de pensamiento crtico y analtico,
        creatividad, organizacin, trabajo en equipo, valores y
        comportamiento tico.
      </p>
      <IntentionForm intention={intetion} />
    </>
  );
};

export default EducationalIntention;
