import React from 'react';
import { useFormContext } from 'react-hook-form';
import FormInput from '../Shared/FormInputs/FormInput';
import PasswordInput from '../Shared/FormInputs/PasswordInput';
import Button from '../Shared/Buttons/Button';

const LoginForm = () => {
  const {
    formState: { isValid, isSubmitting },
  } = useFormContext();
  return (
    <div className="w-96 flex p-8 mx-auto flex-col">
      <div className="mb-2">
        <FormInput
          name="email"
          label="Correo electrnico"
          defaultValue=""
          placeholder="ejemplo@ipn.mx"
          rules={{
            required: 'Ingresa tu correo electrnico',
          }}
        />
      </div>
      <div>
        <PasswordInput
          name="password"
          defaultValue=""
          label="Contrasea"
          placeholder="*************"
          rules={{
            required: 'Ingresa tu contrasea',
          }}
        />
      </div>

      <Button
        className="mt-6"
        primary
        disabled={!isValid || isSubmitting}
      >
        Iniciar sesin
      </Button>
    </div>
  );
};

export default LoginForm;
